import React from "react"
import PropTypes from "prop-types"

import "./base.css"

const Layout = ({ location, children }) => {
  return (
    <div className={location === "home" ? "home-container" : "blog-container"}>
      <main>{children}</main>
      <footer style={{ display: `flex`, alignItems: `center`, justifyContent: `center`, borderTop: `1px solid #ffffff` }}>
        <p style={{ display: `flex`, flex: 1, alignItems: `center`, justifyContent: `space-evenly`, flexWrap: `wrap` }}>
          <a href={`mailto:andrewstolzle@gmail.com`}>andrewstolzle@gmail.com</a>
          <span>© 2020 Andrew Stolzle</span>
        </p>
      </footer>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
